import ProjectCard from "../components/ProjectCard";
import todoGif from "../images/todo-list-example.gif";

const Projects = () => {
  // const pixelArt = { title: "Pixel Art", summary: "Arte em pixel", description: "Neste projeto estava descobrindo o javascript" };
  const todoList = { title: "To-do List", summary: "Lista de afazeres", description: "Descobrindo o DOM, fiz esta lista de afazeres simples", imag: todoGif, site: "https://jrgcast.github.io/todo-list/", repo: "" };
  const recipesApp = { title: "Recipes App", summary: "App de Receitas", description: "Primeiro projeto em grupo com react hooks" };
  const allProjects = [todoList, recipesApp];
  const mappingProjects = allProjects.map(({ title, summary, description, imag, site, repo }) => {
    return <ProjectCard
      title={ title }
      summary={ summary }
      description={ description }
      image={ imag }
      site={ site }
      repo={ repo } />;
  });
  return (
    <section>
      <h1>Meus projetos</h1>
      <main>
        { mappingProjects }
      </main>
    </section>
  );
};

export default Projects;
